.sidebar {
    text-align: center;
    min-width: 300px;
    max-width: 300px;
    background: #373737;
    color: #fff;
    transition: all 0.6s cubic-bezier(0.945, 0.020, 0.270, 0.665);
    transform-origin: center left; /* Set the transformed position of sidebar to center left side. */
    position: fixed;
    height: 100vh;
    z-index: 99999;
}
.sidebar.toggled{
    margin-left: -250px;
    transform: rotateY(100deg); /* Rotate sidebar vertically by 100 degrees. */
}
.sidebarBtn {
    width: 40px;
    height: 40px;
    background: #000;
    margin-top: 8px;
    margin-left: 305px;
    transition: all 0.6s cubic-bezier(0.810, -0.330, 0.345, 1.375);
    position: fixed;
    z-index: 9999;
    border: none;
    outline: none;
}
.sidebarBtn span {
    width: 80%;
    height: 2px;
    margin: 0 auto;
    display: block;
    background: white;
    transition: all 0.7s cubic-bezier(0.810, -0.330, 0.345, 1.375);
}
.sidebarBtn span:first-of-type {
    /* rotate first one */
    transform: rotate(45deg) translate(2px, 2px);
}
.sidebarBtn span:nth-of-type(2) {
    /* second one is not visible */
    opacity: 0;
}
.sidebarBtn span:last-of-type {
    /* rotate third one */
    transform: rotate(-45deg) translate(1px, -1px);
}
.sidebarBtn.toggled{
    margin-left: 0px;
    transition: all 0.7s cubic-bezier(0.810, -0.330, 0.345, 1.375);
    border: none;
    outline: none;
}
.sidebarBtn.toggled span {
    /* no rotation */
    transform: none;
    /* all bars are visible */
    opacity: 1;
    margin: 5px auto;
    border: none;
    outline: none;
}
